.loadingbox {
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9;
}

.loadingbox .bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.8;
}

/* circle animation */

.loading {
  width: 50px;
  height: 50px;

  display: block;
  font-size: 0;
  color: #369;
}

.loading,
.loading > div {
  position: relative;
  box-sizing: border-box;
}

.loading > div {
  display: inline-block;
  float: none;
  background-color: currentColor;
  border: 0 solid currentColor;

  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  opacity: 0;
  animation: ball-scale-multiple 1s 0s linear infinite;
}

.loading > div:nth-child(2) {
  animation-delay: 0.2s;
}

.loading > div:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes ball-scale-multiple {
  0% {
    opacity: 0;
    transform: scale(0.1);
  }

  5% {
    opacity: 0.7;
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}
