.carview {
  width: 5rem;
  height: 2rem;
  padding: 10px;
  display: flex;
  flex-direction: row;
  flex-flow: row;
  align-items: center;

  position: relative;
  border: 1px solid rgba(25, 186, 139, 0.17);
  background: rgba(0, 0, 0, 0.2);
  z-index: 9;
}

.carview::before {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 10px;
  height: 10px;
  border-top: 2px solid #02a6b5;
  border-left: 2px solid #02a6b5;
  border-radius: 20%;
}

.carview::after {
  position: absolute;
  top: 0;
  right: 0;
  content: '';
  width: 10px;
  height: 10px;
  border-top: 2px solid #02a6b5;
  border-right: 2px solid #02a6b5;
  border-radius: 20%;
}

.carview .panel-footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

.carview .panel-footer::before {
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  width: 10px;
  height: 10px;
  border-bottom: 2px solid #02a6b5;
  border-left: 2px solid #02a6b5;
  border-radius: 20%;
}

.carview .panel-footer::after {
  position: absolute;
  bottom: 0;
  right: 0;
  content: '';
  width: 10px;
  height: 10px;
  border-bottom: 2px solid #02a6b5;
  border-right: 2px solid #02a6b5;
  border-radius: 20%;
}

.carview .info {
  color: white;
  font-size: 0.125rem;
  flex: 1;
  font-size: 0.15rem;
  line-height: 0.28rem;
}
.carview .carbox {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 0.1rem;
  width: 2.5rem;
  height: 1.8rem;
}
