* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
@font-face {
  font-family: electronicFont;
  src: url(../../../../public/theme/097/fonts/DS-DIGIT.TTF);
}
.App {
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: Arial, Helvetica, sans-serif;
  background-repeat: no-repeat;
  background-color: #003366;
  /* background-color: #06164a; */
  background-size: cover;
  line-height: 1.15;
  display: flex;
  flex-direction: column;
  flex-flow: column;
}

/* header */
header {
  position: relative;
  width: 100%;
  height: 0.725rem;
  background: url(../../../../public/theme/097/images/head_bg.png) no-repeat top center;
  background-size: 100% 100%;
  z-index: 1;
}
header h1 {
  font-size: 0.3rem;
  color: #fff;
  text-align: center;
  line-height: 0.725rem;
}
header .showTime {
  position: absolute;
  top: 0;
  right: 0.375rem;
  line-height: 0.5rem;
  font-size: 0.2rem;
  color: rgba(255, 255, 255, 0.7);
}

/* mainbox */
.mainbox {
  z-index: 1;
  width: 100%;
  flex: 1;
  overflow: hidden;
  min-width: 1024px;
  padding: 0 0.125rem 0.125rem 0.125rem;
  display: flex;
}
.mainbox .column {
  display: flex;
  flex-direction: column;
  flex-flow: column;
}

/* panel */

.panel {
  position: relative;
  border: 1px solid rgba(25, 186, 139, 0.17);
  background: rgba(0, 0, 0, 0.2);
  padding: 0.125rem;
  margin-bottom: 0.1875rem;
  display: flex;
  flex-direction: column;
  flex-flow: column;
  z-index: 9;
}

.panel::before {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 10px;
  height: 10px;
  border-top: 2px solid #02a6b5;
  border-left: 2px solid #02a6b5;
  border-radius: 20%;
}

.panel::after {
  position: absolute;
  top: 0;
  right: 0;
  content: '';
  width: 10px;
  height: 10px;
  border-top: 2px solid #02a6b5;
  border-right: 2px solid #02a6b5;
  border-radius: 20%;
}

.panel > div {
  display: flex;
  flex-direction: column;
  flex-flow: column;
  flex: 1;
}

.panel .panel-title {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.panel .panel-title > ul {
  display: flex;
  height: 50%;
  margin-left: 0.2rem;
}
.panel .panel-title > ul > li {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  color: #369;
  padding: 0 10px;
  font-size: 0.1875rem;
}
.panel .panel-title > ul > li:last-child {
  border-right: none;
}

.panel .panel-title > ul > li.active {
  color: white;
}

.panel .panel-footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

.panel .panel-footer::before {
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  width: 10px;
  height: 10px;
  border-bottom: 2px solid #02a6b5;
  border-left: 2px solid #02a6b5;
  border-radius: 20%;
}

.panel .panel-footer::after {
  position: absolute;
  bottom: 0;
  right: 0;
  content: '';
  width: 10px;
  height: 10px;
  border-bottom: 2px solid #02a6b5;
  border-right: 2px solid #02a6b5;
  border-radius: 20%;
}

.panel h2 {
  flex: 1;
  height: 0.4rem;
  line-height: 0.4rem;
  text-align: center;
  color: #fff;
  font-size: 0.2rem;
  font-weight: 400;
}

.panel h2 a {
  margin: 0 0.1875rem;
  color: #fff;
  text-decoration: underline;
}

.panel .chart {
  height: 100%;
}

.column .panel:last-child {
  margin-bottom: 0;
}

.mainbox .column:first-child .panel,
.mainbox .column:last-child .panel {
  flex: 1;
}

@media screen and (max-width: 1024px) {
  html {
    font-size: 42px !important;
  }
}

@media screen and (min-width: 1920) {
  html {
    font-size: 80px !important;
  }
}

/* label */
.label {
  display: flex;
  position: relative;
}

.label > .bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.6;
}
.label > .line {
  position: absolute;
  background-color: #fff;
  width: 1px;
  height: auto;

  top: 100%;
  left: 50%;
}
.label > .content {
  position: relative;
  font-size: 0.15rem;
  line-height: 0.3rem;
  color: white;
  font-weight: bold;
  padding: 10px 15px;
  /* background: linear-gradient(90deg, #0fc 50%, transparent 0) repeat-x,
    linear-gradient(90deg, #0fc 50%, transparent 0) repeat-x,
    linear-gradient(0deg, #0fc 50%, transparent 0) repeat-y,
    linear-gradient(0deg, #0fc 50%, transparent 0) repeat-y;
  background-size: 200px 1px, 200px 1px, 1px 200px, 1px 200px;
  background-position: 0 0, 0 100%, 0 0, 100% 0;
  animation: linearGradientMove 3s infinite linear; */
}
@keyframes linearGradientMove {
  100% {
    background-position: 200px 0, -200px 100%, 0 -200px, 100% 200px;
  }
}

.label > .content::before,
.label > .content::after {
  content: '';
  position: absolute;
  top: -3px;
  left: -3px;
  right: -3px;
  bottom: -3px;
  border: 1px solid #09c;
  animation: clippath 6s infinite linear;
}

.label > .content::after {
  animation: clippath 6s infinite -3s linear;
}

@keyframes clippath {
  0%,
  100% {
    clip-path: inset(0 0 95% 0);
  }
  25% {
    clip-path: inset(0 95% 0 0);
  }
  50% {
    clip-path: inset(95% 0 0 0);
  }
  75% {
    clip-path: inset(0 0 0 95%);
  }
}

/* 警报 */
.alertview {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99999;
  animation: alertview-ani 500ms ease-in-out infinite alternate;
  display: flex;
  align-items: center;
  justify-content: center;
}

.alertview > .bg {
  z-index: 99998;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 0, 0, 0.1);
}
.alertview > .content {
  z-index: 99999;
  position: absolute;
  color: orangered;
  background-color: rgba(245, 222, 179, 0.9);
  border-radius: 10px;
  padding: 0.1rem;
  font-size: 0.5rem;
}

@keyframes alertview-ani {
  0% {
    box-shadow: 0 0 5rem rgba(255, 0, 0, 0.5) inset;
  }

  100% {
    box-shadow: 0 0 10rem rgba(255, 0, 0, 1) inset;
  }
}
