.no {
  display: block;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 3;
}
.no .no-box {
  /* flex: 1; */
  min-width: 4rem;
  padding: 0.1rem;
  /* background: rgba(101, 132, 226, 0.1); */
  /* margin-right: 0.125rem; */
  position: absolute;
}

.no .no-box:first-child {
  left: 0;
}

.no .no-box:last-child {
  right: 0;
}

.no .no-hd {
  position: relative;
  border: 1px solid rgba(25, 186, 139, 0.17);
  display: flex;
}

.no .no-hd::before {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  border-top: 2px solid #02a6b5;
  border-left: 2px solid #02a6b5;
  top: 0;
  left: 0;
}

.no .no-hd::after {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid #02a6b5;
  border-right: 2px solid #02a6b5;
  right: 0;
  bottom: 0;
}

.no .no-hd ul {
  display: flex;
  flex: 1;
  border-right: 1px solid rgba(25, 186, 139, 0.17);
}

.no .no-hd ul:last-child {
  border-right: none;
}

.no .no-hd ul li {
  position: relative;
  flex: 1;
  text-align: center;
}

.no .no-hd ul li::after {
  content: '';
  position: absolute;
  height: 50%;
  width: 1px;
  background: rgba(255, 255, 255, 0.2);
  right: 0;
  top: 25%;
}

.no .no-hd ul li:last-child::after {
  background: transparent;
}

.no .no-hd ul li .number {
  line-height: 0.5rem;
  font-size: 0.5rem;
  font-family: electronicFont;
  font-weight: bold;
  padding: 0.05rem 0;
  height: 1rem;
}

.total {
  color: white;
}
.idle {
  color: #38cc24;
}
.checkin {
  color: #ec970d;
}
.using {
  color: #ffeb7b;
}
.rented {
  color: #ffeb7b;
}
.booking {
  color: #16eaf1;
}

.no .no-bd {
  display: flex;
}

.no .no-bd ul {
  display: flex;
  flex: 1;
}

.no .no-bd ul li {
  flex: 1;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: center;
  font-size: 0.15rem;
  color: rgba(255, 255, 255, 0.7);
}
